import React, { Component } from 'react'
import Header from './Header';
import unndou from '../unndou.jpg'
import sige from '../sige.jpg'
import ryogo from '../ryogo.jpg'
import tamu from '../tamu.jpg'
import disney from '../disney.jpg'
import kiss from '../kiss.jpg'
import Slider from "react-slick";

import { BrowserRouter, Route, Link } from 'react-router-dom'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function SimpleSlider1 (props) {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      accessibility: true,
      autoplay: true,
      draggable: true,
      centerModel: true,
    };
    return (
      <Slider {...settings}>
        <div>
          <img src={unndou} alt="Logo" style={{width: '95%', margin: 'auto'}}/>
        </div>
        <div>
          <img src={sige} alt="Logo" style={{width: '95%',margin: 'auto'}}/>  
        </div>
        <div>
          <img src={tamu} alt="Kubo" style={{width: '95%',margin: 'auto'}}/>  
        </div>
        <div>
          <img src={ryogo} alt="Logo" style={{width: '95%',margin: 'auto'}}/>  
        </div>
        <div>
          <img src={disney} alt="Logo" style={{width: '95%',margin: 'auto'}}/>
        </div>
        <div>
         <img src={kiss} alt="Logo" style={{width: '95%',margin: 'auto'}}/>  
        </div>
      </Slider>
    );
  }
const Schedule = () => (
  <div>
    <Header menu=""/>
    <BrowserRouter>
      <div style={{display: 'flex', width: '100%'}}>
        <div style={{width: '30%'}}>
        <ul className='paddingMin'>
        <li><Link to='/schedule'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/1'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/2'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/3'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/4'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/5'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/6'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/7'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/8'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/9'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/10'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/11'><p className='eventSize'></p></Link></li>
        <li><Link to='/schedule/12'><p className='eventSize'></p></Link></li>
        </ul>
        </div>
        <div  style={{width: '70%'}}>
        <Route exact path='/schedule' component={Home} />
        <Route path='/schedule/1' component={About1} />
        <Route path='/schedule/2' component={About2} />
        <Route path='/schedule/3' component={About3} />
        <Route path='/schedule/4' component={About4} />
        <Route path='/schedule/5' component={About5} />
        <Route path='/schedule/6' component={About6} />
        <Route path='/schedule/7' component={About7} />
        <Route path='/schedule/8' component={About8} />
        <Route path='/schedule/9' component={About9} />
        <Route path='/schedule/10' component={About10} />
        <Route path='/schedule/11' component={About11} />
        <Route path='/schedule/12' component={About12} />
      </div>
      </div>
    </BrowserRouter>
  </div>
)

const Home = () => (
  <div>
    <h1 className='title'></h1>
    <p style={{margin: 10}} className='titleSize2'>4MVP2019AirPods</p>
    <SimpleSlider1/>
  </div>
)
